/* fonts */
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,300&display=swap')
$Roboto: 'Roboto', sans-serif

*
    margin: 0
    padding: 0

.page
    p
        font-family: $Roboto
        margin: 20px

.images
    width: 100%
    height: 90%
    display: flex
    justify-content: center
    align-items: center
    flex-wrap: wrap

    img
        height: 280px
        margin: 20px
